input{
  height: 30px;
  width: 100%;
  padding: 5px 10px;
  box-sizing: border-box;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.8) inset;
  outline: none;
  border: none;
  border-radius: 5px;
}